import React from "react";
import "./Cart.sass";
import {ReactComponent as Close} from "../../svg/Close.svg";
const {Translations: {cart}} = window;

const Cart = ({selected = [], onDelete}) => {
  return (
      <div className="Cart">
          <div className="Cart-title">
              {cart.title}
              &nbsp;
              <span>
                  ({selected.length}/22)
              </span>
          </div>
          <div className="Cart-scroll">
              {selected.map((item, i) => (
                  <div className="Cart-item" key={i}>
                      <img className="Cart-img" src={item.images.card} alt=""/>
                      <div className="Cart-delete" onClick={() => onDelete(item.itemId)}>
                          <Close />
                      </div>
                  </div>
              ))}
          </div>
      </div>
  )
};

export default Cart
